.Header {
    position: relative;
    top:-80px;
    height: 100px;
    background-color: rgba(0, 0, 0, 0.671);
    border-bottom: 1px solid rgba(214, 214, 214, 0.753);
    font-size: 1.1em;
    /* display: flex; */
    /* justify-content: space-between; */
    /* align-items: center; */
    /* padding: 0px 200px; */
}
.header_right, .header_left{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 100%;
    position: relative;
    display: inline-block;
    /* padding: 20px 40px 0 200px; */
    width:50%;
    padding-top: 40px;
    padding-left: 50px;
}
.header_right:hover{
    background-color: rgba(255, 255, 255, 0.171);
}
.header_left:hover{
    background-color: rgba(255, 255, 255, 0.171);
}

.Header:hover {
    -webkit-animation: down 1s forwards;
            animation: down 1s forwards;

}

@-webkit-keyframes down {
    from {
      top: -70px; 
    }
    to {
      top: 0px;
    }
}

@keyframes down {
    from {
      top: -70px; 
    }
    to {
      top: 0px;
    }
}
@media (max-width: 800px){
    .Header{
        font-size: .5em;
        /* color:red; */
    }
}


